import React, { Component } from "react";
import { RouteComponentProps, Route, withRouter } from "react-router-dom";
import styled from "../../styles/styled";
import DefaultRoute from "../DefaultRoute";
import Browse from "../browse/Browse";
import CategoryPlaylists from "../browse/CategoryPlaylists";
import Search from "../search/Search";
import PlaylistTracks from "../tracks/PlaylistTracks";
import AlbumTracks from "../tracks/AlbumTracks";

const Wrapper = styled.div`
  flex: 1;
  height: calc(100% - 50px);
  overflow-y: auto;
  padding: 25px;
  position: relative;
`;

class Routes extends Component<RouteComponentProps> {
  render() {
    const { match } = this.props;
    console.log(this.props);

    return (
      <Wrapper>
        <DefaultRoute from="" to="browse" />
        <Route path={`${match.url}browse`} component={Browse} />
        <Route path={`${match.url}search`} component={Search} />
        <Route
          path={`${match.url}categories/:categoryId/playlists`}
          component={CategoryPlaylists}
        />
        <Route
          path={`${match.url}playlists/:playlistId/tracks`}
          component={PlaylistTracks}
        />
        <Route
          path={`${match.url}albums/:albumId/tracks`}
          component={AlbumTracks}
        />
      </Wrapper>
    );
  }
}

export default withRouter(Routes);
